import React, { useEffect, useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
// import axios from "axios"
// material-ui
import {
  Button,
  FormHelperText,
  Grid,
  Link,
  IconButton,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  Stack,
} from '@mui/material';
// third party
import * as Yup from 'yup';
import { Formik } from 'formik';

// project import
import AnimateButton from 'components/@extended/AnimateButton';

// assets
import { EyeOutlined, EyeInvisibleOutlined } from '@ant-design/icons';

// ============================|| FIREBASE - LOGIN ||============================ //
import { useNavigate } from 'react-router-dom';
import toastAlert from 'components/ToastAlert/index';
import { post } from 'Urls/api';
import ClipLoader from 'react-spinners/ClipLoader';
const AuthLogin = () => {
  const navigate = useNavigate()

  const [showPassword, setShowPassword] = React.useState(false);
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  useEffect(() => {
    const userSession = JSON.parse(localStorage.getItem("@UserSession"));
    if (userSession === null || userSession === undefined) {

    } else {
      navigate('/')
    }
    // Do something else if the hash doesn't contain "web"


  }, []);
  return (
    <>
      <Formik
        initialValues={{
          email: '',
          password: '',
          submit: null
        }}
        validationSchema={Yup.object().shape({
          email: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
          password: Yup.string().max(255).required('Password is required')
        })}
        onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
          try {
            console.log(values)
            let Email_Address = values.email
            const postData = {
              email: values.email,
              password: values.password,
              type: 'admin'
            };
            try {
              const apiData = await post('user/login', postData); // Specify the endpoint you want to call
              console.log(apiData)
              if (apiData.error) {
                console.log("error true")
                toastAlert("error", apiData.message)
              } else {

                localStorage.setItem("@UserSession", JSON.stringify({ user: apiData.data }));
                navigate('/')
                setStatus({ success: false });
                setSubmitting(false);
              }
            } catch (error) {
              toastAlert("error", "Something Went Wrong")
              setStatus({ success: false });
              setSubmitting(false);
            }
          } catch (err) {
            toastAlert("error", "Something Went Wrong")

            setStatus({ success: false });
            setErrors({ submit: err.message });
            setSubmitting(false);
          }
        }}
      >
        {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
          <form noValidate onSubmit={handleSubmit}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Stack spacing={1}>
                  <InputLabel htmlFor="email-login">Email Address</InputLabel>
                  <OutlinedInput
                    id="email-login"
                    type="email"
                    value={values.email}
                    name="email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    // placeholder="Enter email address"
                    fullWidth
                    error={Boolean(touched.email && errors.email)}
                  />
                  {touched.email && errors.email && (
                    <FormHelperText error id="standard-weight-helper-text-email-login">
                      {errors.email}
                    </FormHelperText>
                  )}
                </Stack>
              </Grid>
              <Grid item xs={12}>
                <Stack spacing={1}>
                  <InputLabel htmlFor="password-login">Password</InputLabel>
                  <OutlinedInput
                    fullWidth
                    error={Boolean(touched.password && errors.password)}
                    id="-password-login"
                    type={showPassword ? 'text' : 'password'}
                    value={values.password}
                    name="password"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowPassword}
                          onMouseDown={handleMouseDownPassword}
                          edge="end"
                          size="large"
                        >
                          {showPassword ? <EyeOutlined /> : <EyeInvisibleOutlined />}
                        </IconButton>
                      </InputAdornment>
                    }
                  // placeholder="Enter password"
                  />
                  {touched.password && errors.password && (
                    <FormHelperText error id="standard-weight-helper-text-password-login">
                      {errors.password}
                    </FormHelperText>
                  )}
                </Stack>
              </Grid>
              <Grid item xs={12} sx={{ mt: -1 }}>
                <Stack direction="row" justifyContent="space-between" alignItems="center" spacing={2}>
                  <span></span>
                  <Link variant="h6" component={RouterLink} to="/verify_account" color="text.primary">
                    Forgot Password?
                  </Link>
                </Stack>
              </Grid>

              {errors.submit && (
                <Grid item xs={12}>
                  <FormHelperText error>{errors.submit}</FormHelperText>
                </Grid>
              )}
              <Grid item xs={12}>
                <AnimateButton>
                  <Button disableElevation
                  startIcon={isSubmitting?
                    <ClipLoader color="secondary" loading={isSubmitting} size={25} />:null

                  }
                   disabled={isSubmitting} fullWidth size="large" type="submit" variant="contained" color="primary">
                    Login
                  </Button>
                </AnimateButton>
              </Grid>

            </Grid>
          </form>
        )}
      </Formik>
    </>
  );
};

export default AuthLogin;
